*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  background-color: hsl(226deg, 43%, 10%);
  color: white;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cards-box {
  width: min(90%, 500px);
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.card-main {
  position: relative;
  min-height: 9.5em;
}
.card-main__top {
  position: absolute;
  top: 0;
  width: 100%;
  background-color: hsl(246deg, 80%, 60%);
  display: flex;
  align-items: center;
  gap: 1em;
  border-radius: 0.75em;
  padding: 1em;
  line-height: 1.2;
  z-index: 1;
}
.card-main__top-imgbox {
  width: 4em;
  height: 4em;
  border-radius: 50%;
  border: 2px solid #fff;
}
.card-main__top-imgbox img {
  object-fit: cover;
}
.card-main__top-userinfo .static-text {
  font-weight: lighter;
  color: hsl(236deg, 100%, 87%);
}
.card-main__top-userinfo .user-fullname {
  font-weight: 300;
  font-size: 2.4rem;
}
.card-main__bottom {
  display: flex;
  justify-content: space-around;
  position: absolute;
  bottom: 0;
  min-height: 35%;
  width: 100%;
  padding: 0 1em;
  background-color: hsl(235deg, 46%, 20%);
  border-radius: 0 0 0.75em 0.75em;
}
.card-main__bottom::before {
  content: "";
  position: absolute;
  top: -50%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsl(235deg, 46%, 20%);
  z-index: -1;
}
.card-main__bottom-btn {
  background: none;
  border: none;
  color: hsl(235deg, 45%, 61%);
  transition: color 0.3s;
  cursor: pointer;
}
.card-main__bottom-btn:hover {
  color: hsl(236deg, 100%, 87%);
}
.card-main__bottom-btn.active {
  color: hsl(236deg, 100%, 87%);
}

.card {
  position: relative;
  min-height: 9.5em;
}
.card__top {
  position: absolute;
  top: 0;
  left: 0;
  background-color: hsl(15deg, 100%, 70%);
  width: 100%;
  height: 50%;
  border-radius: 1em 1em 0 0;
  overflow: hidden;
}
.card__top img {
  position: absolute;
  top: -8%;
  right: 1em;
}
.card__main {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  align-content: center;
  position: absolute;
  top: 2.5em;
  left: 0;
  width: 100%;
  height: calc(100% - 2.5em);
  padding: 1.5em;
  border-radius: 1em;
  background-color: hsl(235deg, 46%, 20%);
  transition: background-color 0.3s;
}
.card__main:hover {
  background-color: hsl(246deg, 80%, 60%);
}
.card__main-title {
  font-weight: bold;
  grid-column: 1/2;
  grid-row: 1/2;
}
.card__main-content {
  font-size: 3rem;
  font-weight: lighter;
  grid-column: 1/2;
  grid-row: 2/3;
}
.card__main .dots-icon {
  width: 1.2em;
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: end;
  align-self: center;
  cursor: pointer;
}
.card__main-subcontent {
  white-space: nowrap;
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: end;
  align-self: center;
}

.card.work .card__top {
  background-color: hsl(15deg, 100%, 70%);
}

.card.play .card__top {
  background-color: hsl(195deg, 74%, 62%);
}

.card.study .card__top {
  background-color: hsl(348deg, 100%, 68%);
}

.card.exercise .card__top {
  background-color: hsl(145deg, 58%, 55%);
}

.card.social .card__top {
  background-color: hsl(264deg, 64%, 52%);
}

.card.self-care .card__top {
  background-color: hsl(43deg, 84%, 65%);
}

@media (min-width: 960px) {
  .container {
    width: min(90%, 1200px);
    margin-inline: auto;
    height: 100vh;
  }

  .cards-box {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .card-main {
    grid-column: 1/-1;
    grid-row: 1/2;
  }
}
@media (min-width: 1330px) {
  .cards-box {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 22rem);
    width: 90%;
  }

  .card-main {
    grid-column: 1/2;
    grid-row: 1/-1;
    min-height: 12em;
  }
  .card-main__top {
    height: 20em;
    flex-direction: column;
    align-items: start;
    padding: 1.4em;
  }
  .card-main__top-imgbox {
    width: 5em;
    height: 5em;
  }
  .card-main__top-userinfo .user-fullname {
    font-size: 3.6rem;
  }
  .card-main__bottom {
    min-height: 40%;
    flex-direction: column;
    justify-content: end;
    align-items: start;
    gap: 0.55em;
    padding: 1.4em;
  }

  .card__main-content {
    grid-column: 1/3;
    grid-row: 2/3;
  }
  .card__main-subcontent {
    grid-column: 1/3;
    grid-row: 3/4;
    justify-self: start;
    align-self: start;
    color: hsl(236deg, 100%, 87%);
  }
  .card__main .dots-icon {
    align-self: start;
  }
}